// Progress Bars
// --------------------------------------------------

@import "mixins";
@import "variables";
@import "../vendor/bootstrap-sass/assets/stylesheets/bootstrap/mixins";
@import "../vendor/bootstrap-sass/assets/stylesheets/bootstrap/variables";


.progress,
.progress > .progress-bar {
  @include box-shadow(none);
  &, .progress-bar {
    border-radius: $progress-bar-border-radius;
  }
}

/* size variation */
.progress.sm,
.progress-sm {
  height: 10px;
  &, .progress-bar {
    border-radius: $progress-bar-sm-border-radius;
  }
}

.progress.xs,
.progress-xs {
  height: 7px;
  &, .progress-bar {
    border-radius: $progress-bar-xs-border-radius;
  }
}

.progress.xxs,
.progress-xxs {
  height: 3px;
  &, .progress-bar {
    border-radius: $progress-bar-xs-border-radius;
  }
}

/* Vertical bars */
.progress.vertical {
  position: relative;
  width: 30px;
  height: 200px;
  display: inline-block;
  margin-right: 10px;
  > .progress-bar {
    width: 100%;
    position: absolute;
    bottom: 0;
  }

  //Sizes
  &.sm,
  &.progress-sm {
    width: 20px;
  }

  &.xs,
  &.progress-xs {
    width: 10px;
  }
  &.xxs,
  &.progress-xxs {
    width: 3px;
  }
}

//Progress Groups
.progress-group {
  .progress-text {
    font-weight: 600;
  }
  .progress-number {
    float: right;
  }
}

/* Remove margins from progress bars when put in a table */
.table {
  tr > td .progress {
    margin: 0;
  }
}

// Variations
// -------------------------
.progress-bar-light-blue,
.progress-bar-primary {
  @include progress-bar-variant($blue-light);
}

.progress-bar-green,
.progress-bar-success {
  @include progress-bar-variant($green);
}

.progress-bar-aqua,
.progress-bar-info {
  @include progress-bar-variant($aqua);
}

.progress-bar-yellow,
.progress-bar-warning {
  @include progress-bar-variant($yellow);
}

.progress-bar-red,
.progress-bar-danger {
  @include progress-bar-variant($red);
}
